import React, { useEffect, useState } from "react";
import { Button, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useActions } from "../../helpers/hooks/useActions";
import AddUserModal from "../AddUserModal";
import DeleteUserModal from "../DeleteUserModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faEdit } from "@fortawesome/free-solid-svg-icons";
import "./UsersTableStyle.css";

const UsersTable = () => {
  const [toggleModal, setToggleModal] = useState(false);
  const [toggleDeleteModal, setToggleDeleteModal] = useState(false);
  const [selectedUser, setSelectedUser] = useState(null);
  const { addOrEditUserSuccess, removeUserSuccess, users } = useSelector(
    (state) => state
  );
  const { getUsers } = useActions();

  useEffect(() => {
    getUsers();
  }, []);

  useEffect(() => {
    if (addOrEditUserSuccess || removeUserSuccess) {
      getUsers();
      setToggleModal(false);
      setToggleDeleteModal(false);
      setSelectedUser(null);
    }
  }, [addOrEditUserSuccess, removeUserSuccess, getUsers]);
  return (
    <div>
      <div className="d-flex justify-content-between p-4 border">
        <h4>Users</h4>
        <Button onClick={() => setToggleModal(true)}>New User</Button>
      </div>
      <Table responsive striped bordered hover>
        <thead>
          <tr>
            <th className="text-center">Full name</th>
            <th className="text-center">Email</th>
            <th className="text-center">Address</th>
            <th className="text-center">Phone</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {users?.map((user) => {
            return (
              <tr key={user._id}>
                <td data-label="Full name">{user.name + " " + user.surname}</td>
                <td data-label="Email">{user.email}</td>
                <td data-label="Address">{user.address}</td>
                <td data-label="Phone">{user.phone}</td>
                <td>
                  <div className="d-flex flex-column">
                    <Button
                      className="my-1"
                      onClick={() => {
                        setToggleModal(true);
                        setSelectedUser(user);
                      }}
                    >
                      <FontAwesomeIcon icon={faEdit} />
                    </Button>
                    <Button
                      variant="danger"
                      onClick={() => {
                        setToggleDeleteModal(true);
                        setSelectedUser(user);
                      }}
                    >
                      <FontAwesomeIcon icon={faTrash} />
                    </Button>
                  </div>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>
      {toggleModal && (
        <AddUserModal
          onClose={() => {
            setToggleModal(false);
            setSelectedUser(null);
          }}
          user={selectedUser}
        />
      )}
      {toggleDeleteModal && (
        <DeleteUserModal
          user={selectedUser}
          onClose={() => {
            setToggleDeleteModal(false);
            setSelectedUser(null);
          }}
        />
      )}
    </div>
  );
};

export default UsersTable;
